import React from 'react';

import Section from '../section';
import SummaryItem from '../summary-item';

const SectionGames = ({ games }) => {
  if (!games.length) return null;

  return (
    <Section title="Games">
      {games.map((item) => (
        <SummaryItem
          key={item.name}
          name={item.name}
          description={item.description}
          link={item.link}
        />
      ))}
    </Section>
  );
};

export default SectionGames;
